import { Component, OnInit, OnDestroy, ViewEncapsulation } from '@angular/core';
import { VERSION } from '@angular/material';
import { WorkItemsService } from '../../../shared/shared-services/work-items/work-items.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-dropcounts',
  templateUrl: './dropcounts.component.html',
  styleUrls: ['./dropcounts.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DropcountsComponent implements OnInit, OnDestroy {
  private paramsSubscription: Subscription;

  searchText: string = '';
  public loading : boolean = false;  
  data: any;
  instanceId: any;
  showSearch: boolean;

  constructor(private _workitemService: WorkItemsService) {
    this.loading = this._workitemService.dropCountsLoader;
    this.paramsSubscription = this._workitemService.getObj.subscribe((data) => {
      this.data = data;
      this.loading = false;
      console.log(this.loading);
      // if(this.data){
      //   for(var item in this.data.result.data){
      //     let aa = Object.values(this.data.result.data[item]);
      //     for (var item1 of aa){
      //       console.log(item1["timestamp"]);
      //     }
      //  }
      // }

    })
  }

  ngOnInit() {
    this.data = undefined;
  }

  ngOnDestroy() {
    this.paramsSubscription.unsubscribe();
    this.data = undefined;
    this._workitemService.sendObj.next(null);
  }

  keyUpEvent(value) {
    this.loading = true;
    if (value == '') {
      this.loading = false;
      return;
    }
    if (value.replace(/\s*,\s*/g, ',').includes(",")) {
      this.clearSearch();
      var comma = value.split(',').filter(i => i);
      let obj = {
        "recordId": [comma]
      }
      this._workitemService.getDropcountsData(obj).subscribe((data) => {
        this.loading = false;
        this.data = data;
      })
    }
    else {
      var comma = value.split(',');
      this.clearSearch();
      let obj = {
        "recordId": [comma]
      }
      this._workitemService.getDropcountsData(obj).subscribe((data) => {
        this.loading = false;
        this.data = data;
      })
      // this.showSearch = true;
    }
  }
  clearSearch() {
    this.showSearch = false;
    this.searchText = '';
  }

  checkObj(){
      return (Object.keys(this.data.result.data).length != 0);
  }
}



