import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { MainInitComponent } from './main-init/main-init.component';

import { routeRoot } from './main.routes';

import { SharedModulesModule } from '../shared/shared-modules/shared-modules.module';
import { SharedComponentsModule } from '../shared/shared-components/shared-components.module';
import { ChangePasswordComponent } from './main-init/change-password/change-password.component';
import { DropcountsComponent } from './dropcounts/dropcounts/dropcounts.component';
import { MatExpansionModule } from '@angular/material';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    routeRoot,
    SharedModulesModule,
    SharedComponentsModule,
    MatExpansionModule
  ],
  entryComponents: [ChangePasswordComponent],
  declarations: [MainInitComponent, ChangePasswordComponent, DropcountsComponent]
})
export class MainModule { }



